import React, { PureComponent } from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { delINArray } from 'util'
@connect(
  state => ({ ...state.bomSettingDet }),
  mapDispatchToProps
)
class TableToolDet extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  checkBomType = () => {
    const { tableDataSource, form } = this.props
    let len = tableDataSource && tableDataSource.length || 0
    let bomType = form.getFieldValue('bomType') || {}
    if (bomType.value == '3') {
      if (len == 1) {
        return false
      }
      return true
    } else {
      return true
    }
  }
  // 新增
  handleAdd = () => {
    const { update, tableDataSource } = this.props
    if (!this.checkBomType()) {
      return message.error('BOM类型为粗加工时，只有一个原料')
    }
    const newData = {
      id: Date.now(),
      productCode: '',
      productName: '',
      mrpProp: '',
      unit: '',
      spec: '',
      number: '',
      scrapRate: ''
    }
    let tableData = tableDataSource && Array.isArray(tableDataSource) && tableDataSource.length > 0 ? tableDataSource.concat(newData) : [newData]
    update({ tableDataSource: tableData })
  }
 // 删除
  handleDel = () => {
    const { update, selectedRowKeys, tableDataSource = [], form } = this.props
    if (!this.checkBomType()) {
      return message.error('BOM类型为粗加工时，只有一个原料')
    }
    if (selectedRowKeys && selectedRowKeys.length > 0) {
    // <---------将form表单中的数据清除掉  --------------------->
      const list = form.getFieldValue('bomAbilityList')
      let formArr = delINArray(selectedRowKeys, list)
      formArr && formArr.length > 0 && formArr.map((item, index, arr) => {
        const { valueShare = '', number, scrapRate = '', spec, unit, mrpProp, productName, productCode, id = '' } = item
        let obj = {
          [`bomAbilityList[${index}][number]`]: number,
          [`bomAbilityList[${index}][valueShare]`]: valueShare,
          [`bomAbilityList[${index}][scrapRate]`]: scrapRate,
          [`bomAbilityList[${index}][spec]`]: spec,
          [`bomAbilityList[${index}][unit]`]: unit,
          [`bomAbilityList[${index}][mrpProp]`]: mrpProp,
          [`bomAbilityList[${index}][productName]`]: productName,
          [`bomAbilityList[${index}][productCode]`]: productCode,
          [`bomAbilityList[${index}][id]`]: id
        }
        form.setFieldsValue(obj)
      })
    // ---------------------> 将form表单中的数据清除掉   --------------------->
      update(
        {
          tableDataSource: delINArray(selectedRowKeys, tableDataSource),
          selectedRowKeys: []
        })
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    return (
      <div className={style.table_tool_det_bom}>
        <span onClick={this.handleAdd} className={style.table_tool_det_box}>
          <Button type="primary">新增</Button>
        </span>
        <span onClick={this.handleDel}>
          <Button type="primary" style={{ cursor: 'pointer' }}>删除</Button>
        </span>
      </div>
    )
  }
}
export default withRouter(TableToolDet)



