import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_BOMSETTING_DETS_PAGE,
  API_PRODUCTIONCODE_DET,
  API_BOMSETTING_DETS_DETAIL,
  API_BOMSETTING_DETS_CREATE,
  API_BOMSETTING_DETS_UPDATE
} from 'api'
//同步
export const update = object => ({
  type: actionType.BOMSETTING_DET_UPDATE,
  data: object
})

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_DET_UPDATE,
    data: {
      tableLoading: true
    }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({
    api: API_BOMSETTING_DETS_PAGE,
    ...reqData
  })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.BOMSETTING_DET_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}

// 创建/编辑
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_DET_UPDATE,
    data: {
      modalTuttonLoading: true
    }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_BOMSETTING_DETS_UPDATE
      : API_BOMSETTING_DETS_CREATE
  getData({
    api,
    ...reqData
  })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.BOMSETTING_DET_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}
// 获取 - 单个BOM数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({
    api: API_BOMSETTING_DETS_DETAIL,
    ...reqData
  })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        bomType = '',
        calcFactor = '',
        locationCode = '',
        locationName = '',
        productCode = '',
        productName = '',
        spec = '',
        status = '',
        unit = '',
        bomAbilityOneList = []
      } = result
      try {
        const bomTypeValue =
          bomType == 1
            ? '组合型'
            : bomType == 2
            ? '分解型'
            : bomType == 3
            ? '粗加工'
            : '--'
        const statusValue = status == 1 ? '启用' : status == 0 ? '禁用' : '--'
        if (bomAbilityOneList && bomAbilityOneList.length > 0) {
          bomAbilityOneList.map((item, index, arr) => {
            const {
              productCode = '',
              valueShare = '',
              scrapRate = '',
              number = ''
            } = item
            item.productCode = {
              value: `${productCode}`,
              children: `${productCode}`
            }
            item.valueShare = String(valueShare)
            item.scrapRate = String(scrapRate)
            item.number = String(number)
          })
        }
        let dataFormater = {
          ...result,
          calcFactor,
          productName,
          spec,
          unit,
          location: {
            value: `${locationCode}`,
            children: `${locationCode} ${locationName}`
          },
          bomType: {
            value: `${bomType}`,
            children: `${bomTypeValue}`
          },
          status: {
            value: `${status}`,
            children: `${statusValue}`
          },
          productCode: {
            value: `${productCode}`,
            children: `${productCode}`
          }
        }
        dispatch({
          type: actionType.BOMSETTING_DET_UPDATE,
          data: {
            modalData: dataFormater,
            bomTypeSelected: bomType,
            tableDataSource: bomAbilityOneList
          }
        })
      } catch (error) {
        console.log(error)
      }
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}
// 获取 - 单个商品数据
export const getSingleProductDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({
    api: API_PRODUCTIONCODE_DET,
    ...reqData
  })
    .then(data => {
      console.log('res data is', data)
      const { result } = data
      let dataFormater = {
        ...result
      }
      callBack && callBack(dataFormater)
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}



