import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import style from './components/style/Style.scss'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_GET_PRODUCTION_CODEANDNAME
} from 'api'
import { CsxFormSelect, CsxFormSearch } from 'Form'
import { getUrlDataObj, countTableScroll, operationalUnit } from 'util'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  InputNumber,
  Table,
  message
} from 'antd'
const FormItem = Form.Item
@connect(
  state => ({ ...state.bomSettingDet }),
  mapDispatchToProps
)
class AppIndex extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      editOrCreate: '',
      permissionBlooean: '', // 是否允许编辑  'allowEdit' -- false 、 'forbiddenEdit' -- true
      bomId: ''
    }
  }
  componentDidMount() {
    this.initialFunc()
  }
  componentWillUnmount() {
    const { update } = this.props
    update({
      modalData: '',
      tableDataSource: '',
      searchResult: []
    })
  }
  initialFunc = () => {
    this.loadDet() // 初始化操作1: 获取bom详情
  };
  loadDet = () => {
    const { location = {}, match = {}, update, getSingleDet } = this.props
    const { search = '' } = location
    const { params = '' } = match
    const { id = '' } = params
    const { editOrCreate = '', permissionFlag = '' } = getUrlDataObj(search || '')
    const newData = {
      id: Date.now(),
      productCode: '',
      productName: '',
      mrpProp: '',
      unit: '',
      spec: '',
      number: '',
      scrapRate: ''
    }
    update({ tableDataSource: [newData] })
    if (id) {
      //编辑
      const params = {
        urlParams: [`${id}`]
      }
      this.setState({
        bomId: id
      })
      getSingleDet(params)
    }
    this.setState({
      editOrCreate,
      permissionBlooean: permissionFlag === 'allowEdit' ? true : false
    })
  };
  componentWillReceiveProps(nextProps) {}
  // 改变BOM类型
  onBomSelect = values => {
    const { update } = this.props
    const { value = '' } = values
    update({
      bomTypeSelected: value // 1: 组合  2: 分解  3: 粗加工
    })
  };
  handleSelectChange = (typeSource, data) => {
    const { update, modalData = {} } = this.props
    if (typeSource == 'location') {
      // 更改地点编码 -- 筛选地点编码下的商品
      update({
        modalData: { ...modalData, location: data }
      })
    }
  };
  handleSubmit = e => {
    const { form, modalData, handleAddOrEdit, history } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = { ...values }
        let valueShareArr = []
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'bomType') {
              reqObj.bomType = reqObj[i].value
            }
            if (i == 'location') {
              reqObj.location = reqObj[i].children
            }
            if (i == 'status') {
              reqObj.status = reqObj[i].value
            }
            if (i == 'productCode') {
              reqObj.productCode = reqObj[i].value
            }
          }
          if (i == 'bomAbilityList') {
            const bomAbilityList = reqObj[i]
            bomAbilityList &&
              bomAbilityList.length > 0 &&
              bomAbilityList.map((item, index, arr) => {
                item.productCode = item.productCode && item.productCode.value
                if (this.state.editOrCreate == 'create') {
                  delete item.id
                }
                valueShareArr.push(item.valueShare)
              })
          }
        }
        if (reqObj.bomType && reqObj.bomType == '2' && operationalUnit.add(valueShareArr) != 100) {
          return message.error('成品行项目价值占比总数不等于100，请确认!')
        }
        const bomAbilityList = reqObj.bomAbilityList
        if (reqObj.bomType && reqObj.bomType == '3') {
          if (bomAbilityList && bomAbilityList.length != 1) {
            return message.error('BOM类型为粗加工时，只有一个原料!')
          }
          if (
            bomAbilityList &&
            bomAbilityList[0] &&
            bomAbilityList[0].productCode != reqObj.productCode
          ) {
            return message.error('BOM类型为粗加工时，成品和原料编码必须一致!')
          }
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, () => {
          message.success(msg)
          history.replace('/mms/settingCenter/productManage/bomSetting')
        })
      } else {
        this.forceUpdate()
      }
    })
  };
  linkToPage = () => {
    const { history, bomTypeSelected = '' } = this.props
    const { bomId = '' } = this.state
    history.push(
      `/mms/settingCenter/costCount/${bomId}?bomTypeSelected=${bomTypeSelected}`
    )
  };
  renderFormItem = () => {
    const {
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData = {}
    } = this.props
    console.log('this.props is', this.props)
    const { editOrCreate, permissionBlooean } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    const typeOptions = [
      { key: '1', value: '组合型' },
      { key: '2', value: '分解型' },
      { key: '3', value: '粗加工' }
    ]
    const statusOptions = [
      { key: '1', value: '启用' },
      { key: '0', value: '禁用' }
    ]
    return (
      <div className={style.form_item_det}>
        <Row type="flex" justify="start" className={style['table-tool-det']}>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Col>
                {editOrCreate === 'edit' ? (
                  <Button
                    style={{ marginRight: '28px' }}
                    onClick={this.linkToPage}
                  >
                    成本测算
                  </Button>
                ) : null}
                {
                  permissionBlooean ?
                  <Button
                  type="primary"
                  htmlType="submit"
                  style={{
                    marginRight: '0px',
                    marginBottom: '12px',
                    width: '80px'
                  }}
                  loading={modalTuttonLoading}
                >
                  保存
                </Button>
                : null
                }

              </Col>
            </Row>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="地点编码/名称" hasFeedback>
              {getFieldDecorator('location', {
                initialValue: modalData.location,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  allowClear={true}
                  dateApi={API_GET_LOCATION_CODEANDNAME}
                  disabled={!permissionBlooean}
                  selectItem={{
                    data: modalData.location,
                    key: 'code',
                    valueKey: ['code', 'name']
                  }}
                  handleSelectChange={this.handleSelectChange.bind(
                    this,
                    'location'
                  )}
                />
              )}
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="BOM类型" hasFeedback>
              {getFieldDecorator('bomType', {
                initialValue: modalData.bomType,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  disabled={!permissionBlooean}
                  allowClear={true}
                  data={typeOptions}
                  selectItem={{ key: 'key', valueKey: 'value' }}
                  handleSelectChange={this.onBomSelect}
                />
              )}
            </FormItem>
          </Col>
          <Col span={8}>
            <div>

              <FormItem {...formItemLayout} label="计算因子" hasFeedback>
                {getFieldDecorator('calcFactor', {
                  initialValue: modalData.calcFactor,
                  rules: [
                    {
                      required: true,
                      message: '请填写正整数!',
                      pattern: /^[1-9]\d*$/
                    }
                  ]
                })(<Input disabled={!permissionBlooean} placeholder="请填写正整数" />)}
              </FormItem>

            </div>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="状态" hasFeedback>
              {getFieldDecorator('status', {
                initialValue: modalData.status,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  data={statusOptions}
                  disabled={!permissionBlooean}
                  selectItem={{ key: 'key', valueKey: 'value' }}
                  handleSelectChange={this.handleSelectChange.bind(
                    this,
                    'status'
                  )}
                />
              )}
            </FormItem>
          </Col>
        </Row>
      </div>
    )
  };
  renderResultOption = () => {
    const {
      form: { getFieldDecorator },
      modalData = {}
    } = this.props
    const { bomTypeSelected = '', tableDataSource = [] } = this.props
    const { permissionBlooean } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    return (
      <div>
        <div className={style.form_item_det}>
          <Row type="flex" justify="start">
            <Col span={8}>
              <FormItem {...formItemLayout} label="成品编码" >
                {getFieldDecorator('productCode', {
                  initialValue: (modalData && modalData.productCode) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSearch
                    dateApi={API_GET_PRODUCTION_CODEANDNAME}
                    allowClear={false}
                    initData={(modalData && modalData.productCode) || ''}
                    disabled={modalData && modalData.location ? permissionBlooean ? false : true : true}
                    extraParamsKey={{
                      locationCode:
                        (modalData &&
                          modalData.location &&
                          modalData.location.value) ||
                        ''
                    }}
                    showSearch={false}
                    searchParamsKey="product"
                    selectItem={{ key: 'code', valueKey: ['code', 'name'] }}
                    placeholder="选择地点编码/名称后输入"
                    onChangeSelect={data => {
                      const { form } = this.props
                      form.setFieldsValue({ productName: '', unit: '', spec: '' })
                    }}
                    onSelect={dataVale => {
                      const { getSingleProductDet, update, form } = this.props
                      const { value = '' } = dataVale
                      getSingleProductDet(
                        {
                          searchParams: {
                            code: value,
                            locationCode:
                              (modalData &&
                                modalData.location &&
                                modalData.location.value) ||
                              ''
                          }
                        },
                        data => {
                          update({ modalData: { ...modalData, ...data } })
                          const { productName, unit, spec } = data
                          form.setFieldsValue({ productName, unit, spec })
                          if (bomTypeSelected == '3') {
                            const {
                              mrp,
                              productName,
                              productCode,
                              spec,
                              unit
                            } = data
                            let obj = {
                              [`bomAbilityList[${0}][unit]`]: unit,
                              [`bomAbilityList[${0}][spec]`]: spec,
                              [`bomAbilityList[${0}][productName]`]: productName,
                              [`bomAbilityList[${0}][productCode]`]: {
                                value: `${productCode}`,
                                children: `${productCode}`
                              },
                              [`bomAbilityList[${0}][mrpProp]`]: mrp,
                              [`bomAbilityList[${0}][id]`]:
                                tableDataSource &&
                                tableDataSource.length > 0 &&
                                tableDataSource[0].id
                            }
                            form.setFieldsValue(obj)
                          }
                        }
                      )
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="成品名称">
                {getFieldDecorator('productName', {
                  initialValue: (modalData && modalData.productName) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<Input disabled />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="单位">
                {getFieldDecorator('unit', {
                  initialValue: (modalData && modalData.unit) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<Input disabled />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="规格">
                {getFieldDecorator('spec', {
                  initialValue: (modalData && modalData.spec) || ''
                  // rules: [{
                  //   required: true, message: '请填写!'
                  // }]
                })(<Input disabled />)}
              </FormItem>
            </Col>
          </Row>
        </div>
        <div className={style.form_item_det}>
          {permissionBlooean && this.renderTool()}
          {this.renderResultTable()}
        </div>
      </div>
    )
  };
  renderSourceOption = () => {
    const {
      form: { getFieldDecorator },
      modalData = {}
    } = this.props
    const {
      permissionBlooean
    } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    return (
      <div>
        <div className={style.form_item_det}>
          <Row type="flex" justify="start">
            <Col span={8}>
              <FormItem {...formItemLayout} label="原料编码" >
                {getFieldDecorator('productCode', {
                  initialValue: (modalData && modalData.productCode) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSearch
                    dateApi={API_GET_PRODUCTION_CODEANDNAME}
                    showSearch={false}
                    initData={modalData && modalData.productCode}
                    allowClear={false}
                    disabled={modalData && modalData.location ? permissionBlooean ? false : true : true}
                    searchParamsKey="product"
                    placeholder="选择地点编码/名称后输入"
                    extraParamsKey={{
                      locationCode:
                        (modalData &&
                          modalData.location &&
                          modalData.location.value) ||
                        ''
                    }}
                    selectItem={{ key: 'code', valueKey: ['code', 'name'] }}
                    onChangeSelect={data => {
                      const { form } = this.props
                      form.setFieldsValue({ productName: '', unit: '', spec: '' })
                    }}
                    onSelect={dataVale => {
                      const { getSingleProductDet, update, form } = this.props
                      const { value = '' } = dataVale
                      getSingleProductDet(
                        {
                          searchParams: {
                            code: value,
                            locationCode:
                              (modalData &&
                                modalData.location &&
                                modalData.location.value) ||
                              ''
                          }
                        },
                        data => {
                          update({ modalData: { ...modalData, ...data } })
                          const { productName, unit, spec } = data
                          form.setFieldsValue({ productName, unit, spec })
                        }
                      )
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="原料名称" hasFeedback>
                {getFieldDecorator('productName', {
                  initialValue: (modalData && modalData.productName) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<Input disabled />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="单位">
                {getFieldDecorator('unit', {
                  initialValue: (modalData && modalData.unit) || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<Input disabled />)}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem {...formItemLayout} label="规格">
                {getFieldDecorator('spec', {
                  initialValue: (modalData && modalData.spec) || ''
                  // rules: [{
                  //   required: true, message: '请填写!'
                  // }]
                })(<Input disabled />)}
              </FormItem>
            </Col>
          </Row>
        </div>
        <div className={style.form_item_det}>
          { permissionBlooean && this.renderTool()}
          {this.renderSourceTable()}
        </div>
      </div>
    )
  };
  renderTool = () => (
    <Row type="flex" justify="start">
      <Col span={24}>
        <Tool history={history} form={this.props.form} />
      </Col>
    </Row>
  );
  renderResultTable = () => {
    const { tableLoading, selectedRowKeys } = this.props
    const {
      form: { getFieldDecorator },
      modalData = {},
      tableDataSource = []
    } = this.props
    const { editOrCreate, permissionBlooean } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 0 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    const columns = [
      {
        title: '原料编码',
        width: 120,
        dataIndex: 'productCode',
        key: 'productCode',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem
              {...formItemLayout}
              // label="商品编码/名称"
              // hasFeedback
            >
              {getFieldDecorator(`bomAbilityList[${index}][productCode]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSearch
                  dateApi={API_GET_PRODUCTION_CODEANDNAME}
                  showSearch={false}
                  initData={text}
                  allowClear={false}
                  disabled={modalData && modalData.location ? permissionBlooean ? false : true : true}
                  searchParamsKey="product"
                  extraParamsKey={{
                    locationCode:
                      (modalData &&
                        modalData.location &&
                        modalData.location.value) ||
                      ''
                  }}
                  selectItem={{ key: 'code', valueKey: ['code', 'name'] }}
                  onChangeSelect={data => {
                    const { form } = this.props
                    let obj = {
                      [`bomAbilityList[${index}][unit]`]: '',
                      [`bomAbilityList[${index}][spec]`]: '',
                      [`bomAbilityList[${index}][productName]`]: '',
                      [`bomAbilityList[${index}][mrpProp]`]: '',
                      [`bomAbilityList[${index}][id]`]:
                      record && `${record.id}`
                    }
                    form.setFieldsValue(obj)
                  }}
                  onSelect={dataValue => {
                    const { getSingleProductDet, form } = this.props
                    const { value = '' } = dataValue
                    getSingleProductDet(
                      {
                        searchParams: {
                          code: value,
                          locationCode:
                            (modalData &&
                              modalData.location &&
                              modalData.location.value) ||
                            ''
                        }
                      },
                      data => {
                        if (data) {
                          const { mrp, productName, spec, unit } = data
                          let obj = {
                            [`bomAbilityList[${index}][unit]`]: unit,
                            [`bomAbilityList[${index}][spec]`]: spec,
                            [`bomAbilityList[${index}][productName]`]: productName,
                            [`bomAbilityList[${index}][mrpProp]`]: mrp,
                            [`bomAbilityList[${index}][id]`]:
                              record && `${record.id}`
                          }
                          form.setFieldsValue(obj)
                        }
                      }
                    )
                  }}
                />
              )}
            </FormItem>
          </div>
        )
      },
      {
        title: '原料名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem>
              {getFieldDecorator(`bomAbilityList[${index}][productName]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '评估类',
        width: 120,
        dataIndex: 'mrpProp',
        key: 'mrpProp',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem>
              {getFieldDecorator(`bomAbilityList[${index}][mrpProp]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '单位',
        width: 60,
        dataIndex: 'unit',
        key: 'unit',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem>
              {getFieldDecorator(`bomAbilityList[${index}][unit]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '规格',
        width: 60,
        dataIndex: 'spec',
        key: 'spec',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem>
              {getFieldDecorator(`bomAbilityList[${index}][spec]`, {
                initialValue: text || ''
                // rules: [{
                //   required: true, message: '请填写!'
                // }]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '数量',
        width: 80,
        dataIndex: 'number',
        key: 'number',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem hasFeedback>
              {getFieldDecorator(`bomAbilityList[${index}][number]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: ' ',
                    // message: '请输入正数且不超过两位小数!',
                    pattern: /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/
                  }
                ]
              })(<InputNumber disabled={!permissionBlooean} placeholder="请输入正数" />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '报废率（%）',
        width: 90,
        dataIndex: 'scrapRate',
        key: 'scrapRate',
        render: (text, record, index) => {
          const { id } = record
          return (
            <div className="box_in_table">
              <FormItem hasFeedback>
                {getFieldDecorator(`bomAbilityList[${index}][scrapRate]`, {
                  initialValue: text || '',
                  rules: [
                    {
                      required: true,
                      // message: '请输入0-100的数，且最多有两位小数！',
                      message: ' ',
                      pattern: /^\d\.([1-9]{1,2}|[0-9][1-9])$|^[1-9]\d{0,1}(\.\d{1,2}){0,1}$|^100(\.0{1,2}){0,1}$|^0$/
                    }
                  ]
                })(<InputNumber disabled={!permissionBlooean} placeholder="0～100之间" />)}
              </FormItem>
              {id ? (
                <FormItem
                // hasFeedback
                >
                  {getFieldDecorator(`bomAbilityList[${index}][id]`, {
                    initialValue: id || ''
                  })(<Input hidden={true} type={'hidden'} />)}
                </FormItem>
              ) : (
                ''
              )}
            </div>
          )
        }
      }
    ]
    return (
      <Row>
        <div>
          <div className="table-group table_detail">
            <Table
              rowKey={record => record.id}
              loading={tableLoading}
              columns={columns}
              dataSource={tableDataSource}
              scroll={{ x: countTableScroll(columns) }}
              onChange={this.onChange}
              pagination={false}
              rowSelection={rowSelection}
            />
          </div>
        </div>
        {editOrCreate === 'create' ? null : (
          <div className={style.table_group}>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  创建人:
                </Col>
                <Col span={14}>{modalData && modalData.createBy}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  创建时间:
                </Col>
                <Col span={14}>{modalData && modalData.createTime}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  最后更新人员:
                </Col>
                <Col span={14}>{modalData && modalData.updateBy}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  最后更新时间:
                </Col>
                <Col span={14}>{modalData && modalData.updateTime}</Col>
              </Row>
            </Col>
          </div>
        )}
      </Row>
    )
  };
  renderSourceTable = () => {
    const { tableLoading, selectedRowKeys } = this.props
    const {
      form: { getFieldDecorator },
      modalData = {},
      tableDataSource = []
    } = this.props

    const { editOrCreate, permissionBlooean } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 0 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    const columns = [
      {
        title: '成品编码',
        width: 250,
        dataIndex: 'productCode',
        key: 'productCode',
        render: (text, record, index) => (
            <div className="box_in_table">
              <FormItem
                {...formItemLayout}
              >
                {getFieldDecorator(`bomAbilityList[${index}][productCode]`, {
                  initialValue: text || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSearch
                    dateApi={API_GET_PRODUCTION_CODEANDNAME}
                    showSearch={false}
                    allowClear={false}
                    disabled={modalData && modalData.location ? permissionBlooean ? false : true : true}
                    searchParamsKey="product"
                    extraParamsKey={{
                      locationCode:
                        (modalData &&
                          modalData.location &&
                          modalData.location.value) ||
                        ''
                    }}
                    selectItem={{ key: 'code', valueKey: ['code', 'name'] }}
                    onChangeSelect={data => {
                      console.log('data is', data)
                      const { form } = this.props
                      let obj = {
                        [`bomAbilityList[${index}][unit]`]: '',
                        [`bomAbilityList[${index}][spec]`]: '',
                        [`bomAbilityList[${index}][productName]`]: '',
                        [`bomAbilityList[${index}][mrpProp]`]: '',
                        [`bomAbilityList[${index}][id]`]:
                          record && `${record.id}`
                      }
                      form.setFieldsValue(obj)
                    }
                    }
                    initData={text}
                    onSelect={dataValue => {
                      const { getSingleProductDet, form } = this.props
                      const { value = '' } = dataValue
                      getSingleProductDet(
                        {
                          searchParams: {
                            code: value,
                            locationCode:
                              (modalData &&
                                modalData.location &&
                                modalData.location.value) ||
                              ''
                          }
                        },
                        data => {
                          if (data) {
                            const { mrp, productName, spec, unit } = data
                            let obj = {
                              [`bomAbilityList[${index}][unit]`]: unit,
                              [`bomAbilityList[${index}][spec]`]: spec,
                              [`bomAbilityList[${index}][productName]`]: productName,
                              [`bomAbilityList[${index}][mrpProp]`]: mrp,
                              [`bomAbilityList[${index}][id]`]:
                                record && `${record.id}`
                            }
                            form.setFieldsValue(obj)
                          }
                        }
                      )
                    }}
                  />
                )}
              </FormItem>
            </div>
          )
      },
      {
        title: '成品名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName',
        render: (text, record, index) => (
          <div className="box_in_table">
            {
              <FormItem>
                {getFieldDecorator(`bomAbilityList[${index}][productName]`, {
                  initialValue: text || '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<Input disabled />)}
              </FormItem>
            }
          </div>
        )
      },
      {
        title: '评估类',
        width: 150,
        dataIndex: 'mrpProp',
        key: 'mrpProp',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem
            // hasFeedback
            >
              {getFieldDecorator(`bomAbilityList[${index}][mrpProp]`, {
                initialValue: text,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '单位',
        width: 80,
        dataIndex: 'unit',
        key: 'unit',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem
            // hasFeedback
            >
              {getFieldDecorator(`bomAbilityList[${index}][unit]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '规格',
        width: 80,
        dataIndex: 'spec',
        key: 'spec',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem
            // hasFeedback
            >
              {getFieldDecorator(`bomAbilityList[${index}][spec]`, {
                initialValue: text || ''
                // rules: [{
                //   required: true, message: '请填写!'
                // }]
              })(<Input disabled />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '数量',
        width: 100,
        dataIndex: 'number',
        key: 'number',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem hasFeedback>
              {getFieldDecorator(`bomAbilityList[${index}][number]`, {
                initialValue: text || '',
                rules: [
                  {
                    required: true,
                    // message: '请输入正数且不超过两位小数!',
                    message: ' ',
                    pattern: /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/
                  }
                ]
              })(<InputNumber placeholder="请输入正数" />)}
            </FormItem>
          </div>
        )
      },
      {
        title: '价值占比（%）',
        width: 110,
        dataIndex: 'valueShare',
        key: 'valueShare',
        render: (text, record, index) => {
          const { id = '' } = record
          return (
            <div className="box_in_table">
              <FormItem hasFeedback>
                {getFieldDecorator(`bomAbilityList[${index}][valueShare]`, {
                  initialValue: text || '',
                  rules: [
                    {
                      required: true,
                      // message: '请输入0-100的数，且最多有两位小数！',
                      message: ' ',
                      pattern: /^\d\.([1-9]{1,2}|[0-9][1-9])$|^[1-9]\d{0,1}(\.\d{1,2}){0,1}$|^100(\.0{1,2}){0,1}$/
                    }
                  ]
                })(<InputNumber placeholder="0～100之间" />)}
              </FormItem>
              {id ? (
                <FormItem>
                  {getFieldDecorator(`bomAbilityList[${index}][id]`, {
                    initialValue: id || ''
                  })(<Input hidden={true} type={'hidden'} />)}
                </FormItem>
              ) : (
                ''
              )}
            </div>
          )
        }
      }
    ]
    return (
      <Row>
        <div>
          {
            <div className="table-group table_detail">
              <Table
                rowKey={record => record.id}
                loading={tableLoading}
                columns={columns}
                dataSource={tableDataSource}
                scroll={{ x: countTableScroll(columns) }}
                onChange={this.onChange}
                pagination={false}
                rowSelection={rowSelection}
              />
            </div>
          }
        </div>
        {editOrCreate === 'create' ? null : (
          <div className={style.table_group}>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  创建人:
                </Col>
                <Col span={14}>{modalData && modalData.createBy}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  创建时间:
                </Col>
                <Col span={14}>{modalData && modalData.createTime}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  最后更新人员:
                </Col>
                <Col span={14}>{modalData && modalData.updateBy}</Col>
              </Row>
            </Col>
            <Col span={6}>
              <Row>
                <Col span={10} className={style.bottom_tip}>
                  最后更新时间:
                </Col>
                <Col span={14}>{modalData && modalData.updateTime}</Col>
              </Row>
            </Col>
          </div>
        )}
      </Row>
    )
  };
  render() {
    const { bomTypeSelected = '' } = this.props
    const { editOrCreate = '' } = this.state
    let msg =
      editOrCreate == 'edit'
        ? 'BOM编辑'
        : editOrCreate == 'create'
        ? 'BOM新增'
        : ''
    return (
      <section>
        <Bread route={['设置中心', '商品管理', 'BOM设置', msg]} />
        <div>
          <Form onSubmit={this.handleSubmit} autoComplete="off">
            {this.renderFormItem()}
            {bomTypeSelected == '1' || bomTypeSelected == '3'
              ? this.renderResultOption() // 组合型 || 粗加工
              : bomTypeSelected == '2' // 分解型
              ? this.renderSourceOption()
              : ''}
          </Form>
        </div>
      </section>
    )
  }
}
export default Form.create()(withRouter(AppIndex))



